import { Injectable, Inject } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';

import { TbAuthService } from './auth.service';
import { LoginRequest } from './models/login-request';
import { TbAuthEnvironment } from './models/auth-environment';
import { OperationResult } from './models/operation-result';
import { LoginResponse } from './models/login-response';

@Injectable({
  providedIn: 'root'
})
export class TbAuthGuard implements CanActivate {
  constructor(private authService: TbAuthService, private router: Router, @Inject('env') private env: TbAuthEnvironment) { }

  async canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean> {
    // console.log('ActivatedRouteSnapshot', next, state.url);

    const connection = await this.authService.checkConnection();

    if (!connection) {
      console.log('CONNECTIONDOWN');
      // non sono loggato quindi vado alla login
      if (!state.url.includes('/login')) {
        this.router.navigate(['login']);
      }
      return true;
    }

    if (state.url.includes(this.authService.getLoginPageUrl())) {
      this.authService.clearStorage();
      return true;
    }

    /**
     * Se ricevo jwt e subKey significa che devo fare una "autologin"
     * Creo un LoginRequest con i valori ricevuti, integro con appid letto da environment ed effettuo una login specifica
     * In caso positivo vado in homepage "/"
     */
    const jwt = next.queryParams.hasOwnProperty('jwt') ? next.queryParams.jwt : null;
    const subKey = next.queryParams.hasOwnProperty('subKey') ? next.queryParams.subKey : null;
    if (jwt && subKey) {
      const loginRequest: LoginRequest = new LoginRequest();
      loginRequest.token = jwt;
      loginRequest.subscriptionKey = subKey;
      loginRequest.appId = this.authService.getAppId();

      const loginResponse = (await this.authService.login(loginRequest).catch(err => {
        this.authService.errorMessage = err.error && err.error.Message;
        this.router.navigate(['login']);
        return;
      })) as LoginResponse;

      if (!loginResponse) {
        this.router.navigate(['login']);
        return false;
      }

      if (loginResponse.Result) {
        this.authService.errorMessage = '';
        this.router.navigate([this.authService.getRedirectUrl()]);
        return true;
      }
    }

    /**
     * Se arrivo qua vuol dire che ho giÃ  un token salvato e non scaduto quindi chiedo conferma della validitÃ 
     */
    const authtoken = this.authService.getToken() || '';
    if (authtoken) {
      // ho un token, ma ne verifico la validitÃ 
      const res: OperationResult = (await this.authService.isValidToken(authtoken)) as OperationResult;

      if (res.Result) {
        if (state.url.includes(this.authService.getLoginPageUrl())) this.router.navigate([this.authService.getRedirectUrl()]);
        return true;
      } else {
        this.router.navigate([this.authService.getLoginPageUrl()]);
        return false;
      }

    } else {
      // non sono loggato quindi vado alla login
      if (!state.url.includes(this.authService.getLoginPageUrl())) this.router.navigate([this.authService.getLoginPageUrl()]);
      return true;
    }
  }
}
